*{
  margin: 0;
  box-sizing: border-box;
}

:root{
  --blue: #346799;
  --blue-clear: #7ab9f8;
  --grey: #656565;
  --white: #fff;
  --bg-color: #fff;
  --body-bg-color: #2d333b;
  --button-color: #45a54b;
  --button-color-hover: #347d39;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #2d333b;
  background-color: var(--body-bg-color);
}

#root{
  display: flex;
  justify-content: center;
}

.hide{
  display: none;
}
.App{
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    height: 100%;
    width: 100%;
    max-width: 210mm;
    padding-bottom: 32px;
}

.App > div{
    width: 100%;
}

.App-cv{
    background-color: var(--bg-color);
    height: 100%;
    box-shadow: 5px 5px 3px 3px #ffffff59;
    padding: 32px;
    flex: 1 1;
}

.general-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
}

.App > a {
    position: absolute;
    top: 16px;
    right: 16px
}
  
.App > a svg{
    color: var(--white);
    height: 36px;
}
  
.App > a svg:hover{
    color: var(--blue-clear);
}

.App-header{
    padding: 16px;
    color: #fff;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr -webkit-min-content -webkit-min-content 1fr;
    grid-template-rows: 1fr min-content min-content 1fr;
    align-items: center;
}

.App-header h2{
    border-bottom: 1px solid var(--white);
    width: 40%;
    text-align: center;
    justify-self: center;
    padding: 8px;
}
.App-header .btn-edit-preview{
    justify-self: center;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--white);
    border: 1px solid var(--white);
    margin-top: 16px;
    position: relative;
}

.btn-preview, 
.btn-edit{
    text-align: center;
    width: 112px;
    z-index: 1;
    cursor: pointer;
    padding: 8px;
}

.btn-slider{
    position: absolute;
    left: 0;
    background-color: var(--blue);
    height: 100%;
    width: 50%;
    z-index: 0;
    transition: all 0.3s ease-in-out;
}

.slider-active{
    left: 50%;
}

.edit-input{
    display: flex;
    align-items: center;
    grid-gap: 8px;
    gap: 8px;
}

.edit-input  :nth-child(1){
    padding: 4px;
}

.edit-input button{
    background-color: var(--button-color);
    color: #fff;
    font-weight: 700;
    font-size: .9rem;
    border: 0;
    border-radius: 4px;
    padding: 6px 12px;
}

.edit-input button:hover{
    cursor: pointer;
    background-color: var(--button-color-hover);

}
.general-intro{
    flex: 1.3 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    text-align: justify;
}

.general-intro > *:not([iseditable="false"]):hover{
    cursor: pointer;
}

.general-intro > *:hover:not(div):not([iseditable="false"]):after{
    content: '';
    background: url(data:image/png;base64,);
    background-size: 16px;
    height: 16px;
    width: 16px;
    position: absolute;
}

.general-intro [field="name"]{
    color: var(--blue);
    font-weight: 500;
    font-size: 2.3rem;
}

.general-intro [field="job"]{
    color: var(--grey);
    font-weight: 300;
    font-size: 1.3rem;
}

.general-intro [field="desc"]{
    padding: 8px 0;
    font-size: 1rem;
}
.general-link{
    flex: 1 1;
}

.general-link ul{
    list-style: none;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
}

.general-link li{
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    grid-gap: 8px;
    gap: 8px;
    padding: 4px 0;
}

.general-link li img{
    height: 24px;
}

.general-link ul li p:not([iseditable="false"]):hover{
    cursor: pointer;
}

.general-link li > *:hover:not(div):not([iseditable="false"]):before{
    content: '';
    background: url(data:image/png;base64,);
    background-size: 16px;
    height: 16px;
    width: 16px;
    position: absolute;
    left: -16px;
}
.section{
    border-top: 2px solid var(--grey);
    border-bottom: 2px solid var(--grey);
    text-align: center;
    color: var(--blue);
    margin-top: 32px;
    margin-bottom: 24px;
}
.skills-container{
    display: flex;
    flex-wrap: wrap;
    grid-gap: 16px;
    gap: 16px;
}

.skills-container > button{
    background-color: var(--button-color);
    color: #fff;
    font-weight: 700;
    font-size: 1rem;
    border: 0;
    border-radius: 4px;
    padding: 6px 12px;
}

.skills-container > button:hover{
    cursor: pointer;
    background-color: var(--button-color-hover);
}

.skills{
    padding: 6px 16px;
    background-color: var(--blue);
    color: var(--white);
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.skills > button{
    display: none;
    background: 0;
    border: 0;
    color: rgb(255, 145, 145);
    font-size: 1rem;
    padding: 0 0 0 16px;
}

.skills:hover:not([iseditable="false"]) > button{
    display: flex;
    cursor: pointer;
}

.skills > button:hover {
    color: red;
} 
.work-container{
    display: flex;
    flex-direction: column;
}

.work-container > div:last-of-type{
    margin: 0;
}

.work-date-location{
    font-size: 0.9rem;
    font-style: italic;
    color: var(--grey);
    display: flex;
}

.work-date-location :nth-child(2){
    margin-left: auto;
}

.work-container > button{
    background-color: var(--button-color);
    color: #fff;
    font-weight: 700;
    font-size: .9rem;
    border: 0;
    border-radius: 4px;
    padding: 6px 12px;
    width: 100%;
    margin-top: 24px;
}

.work-container > button:hover{
    cursor: pointer;
    background-color: var(--button-color-hover);
}

.work{
    position: relative;
    margin-bottom: 24px;
    display: flex;
    flex-direction: column;
    grid-gap: 4px;
    gap: 4px;
}

.work ul > button{
    background-color: var(--button-color);
    color: #fff;
    font-weight: 700;
    font-size: .9rem;
    border: 0;
    border-radius: 4px;
    padding: 2px 4px;
}

.work ul > button:hover{
    cursor: pointer;
    background-color: var(--button-color-hover);
}

[field="jobTitle"] {
    font-size: 1.4rem;
    font-weight: 500;
}

[field="company"] {
    font-size: 1.2rem;
    font-weight: 300;
}

.work *:not(ul):not(li):not(div):not([iseditable="false"]):hover{
    cursor: pointer;
    width: -webkit-max-content;
    width: max-content;
}

.work *:hover:not(button):not(ul):not(li):not(div):not([iseditable="false"])::after{
    content: '';
    background: url(data:image/png;base64,);
    background-size: 16px;
    height: 16px;
    width: 16px;
    position: absolute;
}

.work li button{
    opacity: 0;
    background: 0;
    border: 0;
    color: red;
    font-size: .9rem;
    margin: 2px 8px;
}

.work li:hover > button{
    opacity: 1;
    cursor: pointer;
}

.work li > button:hover {
    color: rgb(134, 17, 17);
}

.work > button{
    position: absolute;
    top: 0px;
    right: 0px;
    opacity: 0;
    background: 0;
    border: 0;
    color: red;
    font-size: 1.3rem;
}

.work:hover > button{
    opacity: 1;
    cursor: pointer;
}

.work > button:hover {
    color: rgb(134, 17, 17);
} 


.education-container{
    display: flex;
    flex-direction: column;
}

.education-container > div:last-of-type{
    margin: 0;
}

.education-container > button{
    background-color: var(--button-color);
    color: #fff;
    font-weight: 700;
    font-size: .9rem;
    border: 0;
    border-radius: 4px;
    padding: 6px 12px;
    width: 100%;
    margin-top: 24px;
}

.education-container > button:hover{
    cursor: pointer;
    background-color: var(--button-color-hover);
}

.education{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    position: relative;
    margin-bottom: 24px;
    display: flex;
    flex-direction: column;
    grid-gap: 4px;
    gap: 4px;
}

.education :nth-child(1n){
    width: -webkit-max-content;
    width: max-content;
}

[field="eduTitle"]{
    font-size: 1.3rem;
    font-weight: 500;
}

[field="school"]{
    font-size: 1.2rem;
    font-weight: 300;
}

[field="date"]{
    font-size: 0.8rem;
    font-style: italic;
    color: var(--grey);
}

.education *:hover:not(button):not(div):not([iseditable="false"])::after{
    content: '';
    background: url(data:image/png;base64,);
    background-size: 16px;
    height: 16px;
    width: 16px;
    position: absolute;
}

.education *:not([iseditable="false"]):hover{
    cursor: pointer;
}

.education > button{
    position: absolute;
    top: 0px;
    right: 0px;
    opacity: 0;
    background: 0;
    border: 0;
    color: red;
    font-size: 1.3rem;
}

.education:hover > button{
    opacity: 1;
    cursor: pointer;
}

.education > button:hover {
    color: rgb(134, 17, 17);
} 
