.App{
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    height: 100%;
    width: 100%;
    max-width: 210mm;
    padding-bottom: 32px;
}

.App > div{
    width: 100%;
}

.App-cv{
    background-color: var(--bg-color);
    height: 100%;
    box-shadow: 5px 5px 3px 3px #ffffff59;
    padding: 32px;
    flex: 1;
}

.general-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
}

.App > a {
    position: absolute;
    top: 16px;
    right: 16px
}
  
.App > a svg{
    color: var(--white);
    height: 36px;
}
  
.App > a svg:hover{
    color: var(--blue-clear);
}
